
.header {
  color: #fff;
  .online {
    position: absolute;
    left: 45px;
    font-size: 19px;
    font-family: '时尚中黑简体';
    color: #3ae0e9;
    i {
      background: url(./images/user-icon.png) no-repeat;
      width: 18px;
      height: 18px;
      display: inline-block;
      float: left;
      margin: 27px 8px 0 0;
    }
    span {
      color: #bffd78;
    }
  }
  .header-timer {
    position: absolute;
    top: 20px;
    right: 280px;
    text-align: center;
    text-shadow: 0px 0px 20px #fff;
    line-height: 42px;
    .timer-date {
      margin-top: 4px;
      font-size: 21px;
    }
    .timer-hour {
      font-size: 35px;
    }
  }
  .header-user {
    font-size: 30px;
    position: absolute;
    top: 0px;
    right: 30px;
    .out-system {
      font-size: 20px;
      color: #acafb3;
      padding-left: 5px;
      font-family: '时尚中黑简体';
      cursor: pointer;
      &:hover {
        color: #c3c3c3;
      }
    }
  }
}


