
.pages-left {
  .camera-header {
    position: absolute;
    top: 28px;
    left: 20px;
    font-family: '时尚中黑简体';
    .title {
      color: #fff;
      font-size: 24px;
      padding: 10px 0 0 30px;
      float: left;
    }
    /*总数*/
    .count {
      font-size: 28px;
      color: #b4c7f9;
      float: left;
      width: 90%;
      padding: 30px 0 0 30px;
      p:first-child {
        width: 31%;
      }
      span {
        background: #145191;
        font-size: 18px;
        margin-left: 10px;
        padding: 3px 8px;
        margin-bottom: 5px;
        float: right;
      }
      p.value {
        font-size: 60px;
        color: #44ff86;
        padding-top: 10px;
        text-shadow: 0 0 30px #44ff86;
      }
    }
    .count-list {
      float: left;
      font-family: '微软雅黑';
      margin-top: 15px;
      li {
        background: url(./images/count-bg.png) no-repeat;
        width: 138px;
        height: 84px;
        font-size: 30px;
        padding: 25px 0 0 40px;
        line-height: 45px;
        color: #fff;
        float: left;
        p {
          color: #5e8fff;
        }
      }
    }
  }
}


